import Vue from 'vue';
import App from './App';
import router from './router';
import fastClick from 'fastclick';
import '@/utils/lib-flexible';

if (process.env.NODE_ENV !== 'production') {
  require('eruda').init();
}

if (document) {
  fastClick.attach(document.body);
}

Vue.config.productionTip = false;

new Vue({
  router,
  render: h => h(App)
}).$mount('#app');
