import Vue from 'vue';
import Router from 'vue-router';

Vue.use(Router);

let config = {
  mode: process.env.NODE_ENV === 'production' ? 'history' : 'hash',
  fallback: process.env.NODE_ENV !== 'production',
  scrollBehavior: () => ({ x: 0, y: 0 })
};

export default new Router({
  ...config,
  routes: [
    {
      path: '/',
      name: 'index',
      component: () => import('./views/index.vue')
    }, {
      path: '*',
      redirect: '/'
    }
  ]
});
